<template>
  <div class="about" v-if="array">
    <div class="image__container"><img :src="array.image_url" alt=""></div>
    <div class="content__container" :style="{overflowY: array.synopsis ? 'scroll': 'hidden'}">
      <h1 id="title">{{array.title}}</h1>
      <!-- GENRES -->
      <h1 id="genre" v-if="array.genres">Genres: <span>{{array.genres.map((genre)=>{return genre.name}).join(", ")}}</span></h1>
      <!-- VIDEO -->
      <div id="trailer" :class="{'trailer':expandtr}">
        <h1  @click="expandtr=!expandtr">Trailer:</h1>
        <div v-if="array.trailer_url" class="video">
          <iframe class="iframe" width="320" height="240" controls :src="array.trailer_url"  frameborder="0" allow="encrypted-media" allowfullscreen></iframe>
        </div>
      </div>
      <!-- SYNOPSIS -->
      <div id="synopsis"  :class="{'synopsis':expandsy}">      
        <h1 v-if="array.synopsis" @click="expandsy=!expandsy">Synopsis:</h1>
        <p>{{array.synopsis}}</p>
        <!-- <h1 v-else>Synopsis: <span>Nothing to synopsise</span></h1> -->
      </div>

      <!-- RECOMMENDATIONS -->
      <div id="recommendation" :class="{'recommendation':expandre}">
        <h1 @click="expandre=!expandre">Recommendations:</h1>
        <div v-if="recs.length>0&&expandre" class=recs>
          <router-link v-for="rec in recs" :key="rec" :to="{ name:'Information', params:{id:rec.mal_id} }" class="routerlink">
            <img :src="rec.img_url" alt="">
            <div class="route__title"><p>{{rec.title}}</p></div>
            <div class="route__genre"><p>{{rec.title}}</p></div>
          </router-link>
        </div>
        <div v-else>Nothing to recommend . . .</div>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name:'About',
  data(){ return { 
    array: null, 
    recs: [], 
    hovering: false, 
    expandtr: false,
    expandsy: false, 
    expandre: false } 
    },
  watch:{ '$route.params.id': function(){ this.$router.go() } },
  mounted(){
    this.fetchAnime(this.$route.params.id)
  },
  methods:{ 
    fetchAnime(id){
      fetch(`https://api.jikan.moe/v3/anime/${id}`, {"method": "GET"})
      .then(response => { return response.json() })
      .then(response => { this.array = response })
      .catch(err => { console.error(err); });

      fetch(`https://api.jikan.moe/v3/anime/${id}/recommendations`, { "method": "GET"})
      .then(response => { return response.json() })
      .then(response => {
        const reclength = response.recommendations.length
        for(var i=0; i < ( reclength > 6 ? 7 : reclength);i++) this.recs.push(response.recommendations[i])
        this.recs = this.recs.map(item=> {return {mal_id:item.mal_id,title:item.title, img_url:item.image_url}})
      }).catch(err => { console.error(err) }); 
    }
  }
}
</script>

